// make background full-width

@mixin base() {
    margin-left: auto;
    margin-right: auto;
    max-width: map-get($breakpoints, large);
    padding-bottom: space(2);
    padding-left: space(5);
    padding-right: space(2);
    padding-top: space(2);
    position: relative;

    &::before {
        @include icon($component-panel-info-icon);

        background-repeat: no-repeat;
        background-size: contain;
        content: "";
        left: space(1);
        position: absolute;
        top: space(1);
    }
}

&-global-alert {
    @include base;
    @include panel($panel-info);

    &--positive {
        @include base;
        @include panel($panel-positive);

        &::before {
            @include icon($component-panel-positive-icon);
        }
    }

    &--warning {
        @include base;
        @include panel($panel-warning);

        &::before {
            @include icon($component-panel-warning-icon);
        }
    }

    &--negative {
        @include base;
        @include panel($panel-negative);

        &::before {
            @include icon($component-panel-negative-icon);
        }
    }

    @import "heading";
    @import "content";
    @import "close";
}