@mixin base() {
    @include set-text-style($text-style-heading-4);

    color: $component-link-color-inverse;
    padding-right: 20px;
    text-decoration: none;

    &:hover {
        color: $component-link-color-inverse;
        text-decoration: none;
    }

    [data-content-open="true"] & {
        display: none;
    }
}

&__content-open {
    @include base;

    @include breakpoint(medium) {
        display: none;
    }
}

&__content-controls {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
}