// make background full-width

@mixin base {
    align-items: center;
    background-color: $component-panel-background-color;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
    max-width: map-get($breakpoints, large);
    padding: space(1);

    @include breakpoint(medium) {
        flex-direction: row;
        padding: space(2);
    }
}

&-page-footer {
    @include base;

    @import "branding";
    @import "copyright";
    @import "navigation";
}