$panel-default: (
    bg: $component-panel-background-color,
    text: $component-panel-text-color
);

$panel-info: (
    bg: $component-panel-info-background-color,
    text: $component-panel-info-text-color
);

$panel-positive: (
    bg: $component-panel-positive-background-color,
    text: $component-panel-positive-text-color
);

$panel-warning: (
    bg: $component-panel-warning-background-color,
    text: $component-panel-warning-text-color
);

$panel-negative: (
    bg:$component-panel-negative-background-color,
    text: $component-panel-negative-text-color
);

@mixin panel($type) {
    background-color: map-get($type, bg);
    color: map-get($type, text);
}