@mixin set-text-style($style) {
    font-family: map-get($style, family);
    font-size: map-get($style, mobile-size);
    font-weight: map-get($style, weight);
    line-height: space(map-get($style, mobile-line-height));
    margin-bottom: space(map-get($style, mobile-margin-bottom));
    margin-top: 0;
    text-transform: map-get($style, transform);

    @media (min-width: 1170px) {
        font-size: map-get($style, desktop-size);
        line-height: space(map-get($style, desktop-line-height));
        margin-bottom: space(map-get($style, desktop-margin-bottom));
    }
}
