$clickable-default: (
    bg: $component-clickable-background-color,
    text: $component-clickable-text-color,

    bg-focus: $component-clickable-background-color-focus,
    text-focus: $component-clickable-text-color-focus,

    bg-hover: $component-clickable-background-color-hover,
    text-hover: $component-clickable-text-color-hover,

    bg-active: $component-clickable-background-color-active,
    text-active: $component-clickable-text-color-active,

    bg-disabled: $component-clickable-background-color-disabled,
    text-disabled: $component-clickable-text-color-disabled
);

$clickable-primary: (
    bg: $component-clickable-primary-background-color,
    text: $component-clickable-primary-text-color,

    bg-focus: $component-clickable-primary-background-color-focus,
    text-focus: $component-clickable-primary-text-color-focus,

    bg-hover: $component-clickable-primary-background-color-hover,
    text-hover: $component-clickable-primary-text-color-hover,

    bg-active: $component-clickable-primary-background-color-active,
    text-active: $component-clickable-primary-text-color-active,

    bg-disabled: $component-clickable-primary-background-color-disabled,
    text-disabled: $component-clickable-primary-text-color-disabled
);

$clickable-positive: (
    bg: $component-clickable-positive-background-color,
    text: $component-clickable-positive-text-color,

    bg-focus: $component-clickable-positive-background-color-focus,
    text-focus: $component-clickable-positive-text-color-focus,

    bg-hover: $component-clickable-positive-background-color-hover,
    text-hover: $component-clickable-positive-text-color-hover,

    bg-active: $component-clickable-positive-background-color-active,
    text-active: $component-clickable-positive-text-color-active,

    bg-disabled: $component-clickable-positive-background-color-disabled,
    text-disabled: $component-clickable-positive-text-color-disabled
);

$clickable-negative: (
    bg: $component-clickable-negative-background-color,
    text: $component-clickable-negative-text-color,

    bg-focus: $component-clickable-negative-background-color-focus,
    text-focus: $component-clickable-negative-text-color-focus,

    bg-hover: $component-clickable-negative-background-color-hover,
    text-hover: $component-clickable-negative-text-color-hover,

    bg-active: $component-clickable-negative-background-color-active,
    text-active: $component-clickable-negative-text-color-active,

    bg-disabled: $component-clickable-negative-background-color-disabled,
    text-disabled: $component-clickable-negative-text-color-disabled
);

@mixin clickable($type) {
    background-color: map-get($type, "bg");
    color: map-get($type, "text");
    cursor: pointer;
    text-decoration: none;

    &:focus {
        background-color: map-get($type, "bg-focus");
        color: map-get($type, "text-focus");
    }

    &:hover {
        background-color: map-get($type, "bg-hover");
        color: map-get($type, "text-hover");
    }

    &:active {
        background-color: map-get($type, "bg-active");
        color: map-get($type, "text-active");
    }

    &[disabled],
    &[data-disabled="true"] {
        background-color: map-get($type, "bg-disabled");
        color: map-get($type, "text-disabled");
        cursor: default;
    }
}
