// make background full-width

@mixin base() {
    align-items: center;
    background-color: $component-panel-background-color;
    display: flex;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
    max-width: map-get($breakpoints, large);
    padding: space(1);
    position: relative;

    @include breakpoint(medium) {
        align-items: flex-end;
        padding: space(2);
    }
}

&-page-header {
    @include base;

    @import "branding";
    @import "content";
    @import "content-open";
    @import "content-close";
    @import "navigation";
    @import "site-context";
}
