@mixin base() {
    display: none;
    flex-basis: 100%;
    flex-grow: 1;
    flex-shrink: 1;

    [data-content-open="true"] & {
        display: block;
    }

    [data-content-open="false"] & {
        display: none;
    }
}

&__content {
    @include base;

    @include breakpoint(medium) {
        display: block;
        flex-basis: auto;

        [data-content-open="false"] & {
            display: block;
        }
    }
}