@mixin base() {
    border-top: 2px solid $component-border-color;
    font-size: 0; // remove whitespace between list items
    list-style: none;
    margin: 0;
    padding: 0;
}

&__list {
    @include base;

    //strange to put an @include below other stuff but this is a media query include
    @include breakpoint(medium) {
        border-top: none;
    }

    //possible option for fact that @media needs to come after prop settings
    // @media screen and (min-width: map-get($breakpoints, medium)) {
    //     display: block;
    //     border-top: none;
    // }
}
