@mixin base() {
    @include set-text-style($text-style-heading-4);

    color: $component-link-color-inverse;
    display: none;
    padding-right: 20px;
    text-decoration: none;

    &:hover {
        color: $component-link-color-inverse;
        text-decoration: none;
    }

    [data-content-open="true"] & {
        display: block;
    }
}

&__content-close {
    @include base;

    @include breakpoint(medium) {
        [data-content-open="true"] & {
            display: none;
        }
    }
}