@mixin base() {
    @include set-text-style($text-style-content-2);

    display: inline-block;
}

&__item {
    @include base;

    &::after {
        content: "/";
        margin-bottom: 0;
        margin-left: calc(5px + 0.32em);
        margin-right: 5px;
        margin-top: 0;
    }

    &--active {
        @include base;
    }
}