@mixin base() {
    @include set-text-style($text-style-heading-4);

    color: $component-link-color-inverse;
    display: block;
    padding-bottom: 20px;
    padding-left: 16px;
    padding-top: 16px;
    text-decoration: none;

    &:hover {
        background-color: $component-link-highlight-color-hover;
        color: $component-link-color-inverse-hover;
        text-decoration: none;
    }

    &:active {
        background-color: $component-link-highlight-color-active;
        color: $component-link-color-inverse-active;
    }

    @include breakpoint(medium) {
        padding-bottom: 20px;
        padding-left: 16px;
        padding-right: 16px;
        padding-top: 16px;
    }
}

&__link {
    @include base;

    &--active {
        @include base;
        background-color: $component-link-highlight-color-active;
        color: $component-link-color-inverse-active;
    }
}